import emitter from "./eventDispatcher.js";
import baseEquipment from "./baseEquipment.js";


export default class relayBase extends baseEquipment {

    constructor(scene,x,y,name){
        super(scene,x,y,'hd_relayStation');
        //Always readjust your hit areas :)
        this.mainSprite.setScale(0.5);
        this.setSize(this.mainSprite.displayWidth,this.mainSprite.displayHeight);
        this.mainSprite.setInteractive();
        //this.setInteractive();
        this.baseLine = new Phaser.Curves.Line([ 0, 0, 12, -50 ]);   
        this.userLine = new Phaser.Curves.Line([ 0, 0, 0, 0]);
        this.tethered = false;
        this.bs_target = false;
        this.inUse = false;
        this.range = 300;
        this.name = name
        this.rangeCircle = new Phaser.Geom.Circle(0,0,this.range);
        this.bsCoords = {x: 0, y: 0};
        this.bsName = 'null';
        this.targetCoords = {x: 0, y: 0};
        this.relayIcon = scene.add.sprite(12,-50,'relay_bands','red').setScale(0.5);
        this.add(this.relayIcon);
        scene.input.on('pointerdown',this.mouseClick,this);
        scene.input.setDraggable(this.mainSprite);
        emitter.on('bstoRelay_selected',(eventData) => {
            //Catch clause

            this.bs_target = !this.bs_target;
            if(!this.tethered) {
                this.frequency = eventData.f; 
                this.freqType = eventData.t;
            }
            if(eventData.x == this.bsCoords.x && eventData.y == this.bsCoords.y) return; //No need to run this function if its the same Base Station.
            //Except frequency, which could change.
            this.bsName = eventData.name;
            this.bsCoords = {x: eventData.x,y:eventData.y};
            const localCoords = this.getLocalPoint(this.bsCoords.x,this.bsCoords.y)
           this.mainSprite.input.draggable = false;
           this.baseLine.p0.x = localCoords.x
           this.baseLine.p0.y = localCoords.y
           this.speed =  Math.min(this.baseLine.getLength()/60,8);
           this.duration = 3000 + 2.71 ** this.speed;

           // [this.boxMove, this.paths] = this.newBoxAnim(this.scene,this.handleEnvelope,this.successHandler);
        })     

        emitter.on('user_started', (bsInfo,user) => {
            if(bsInfo.name === this.name){
                emitter.emit('relay_to_bs',{f: this.frequency, name: this.bsName});
                console.log('start');
                this.inUse = true;
                this.mainSprite.input.draggable = false;
                this.toggled =false;
            } else {
                this.bs_target = false;
            }
        })

        emitter.on('user_finished', (bsInfo) => {
            if(bsInfo.name === this.name){
                emitter.emit('relay_to_bs',{f: this.frequency, name: this.bsName});
                console.log('end');

                this.inUse = false;
                this.mainSprite.input.draggable = true;
            }
        });

        emitter.on('bs_unselected', (bsInfo) => {
            if(bsInfo.name === this.bsName && !this.inUse){
                this.mainSprite.input.draggable = true;
            }
        })

        //Draggable Relay :)
    
        scene.input.on('drag', function (pointer, obj, dragX, dragY)
        {
            const cursorLocation = this.scene.cameras.main.getWorldPoint(this.scene.input.activePointer.x, this.scene.input.activePointer.y);

            obj.parentContainer.setPosition(cursorLocation.x,cursorLocation.y);
        }, this);

        scene.input.on('dragend', function (pointer, obj) {
            this.bsCoords = this.getLocalPoint(this.bsCoords.x,this.bsCoords.y);
            this.baseLine.p0.x = this.bsCoords.x
            this.baseLine.p0.y = this.bsCoords.y

        }, this)
    
        
    }

    //Necessary functions:
    //-Ability to be selected as a target by a base station
    //-Ability to select a single user once connected to a base station
    //-Graphical indication of what frequency the relay is using. Colour of bulb?

    
    
    mouseClick(){
      
        if(!this.inUse){
            if(!this.toggled & this.hover) {
                this.tethered = this.bs_target;
                if(this.tethered && !this.inUse){
                    this.toggled = true;
                    console.log('beep');
                    emitter.emit('bs_selected',{x: this.x+12, y: this.y-50, f: this.frequency, t: this.freqType, name: this.name, range: this.range});    
                }       
            } else {
                this.graphics.clear();
                this.toggled = false;
                emitter.emit('bs_unselected', this.name);
            }
        }
 
            
    }
    update(){
        super.update();
        if(this.tethered){
            this.mainSprite.input.draggable = false;
            this.graphics.lineStyle(5, this.frequency, 1.0);
            this.relayIcon.setFrame(this.freqType).setVisible(true);
            this.baseLine.draw(this.graphics);

        } else {
            this.relayIcon.setVisible(false);

        }
    }
}